
import { IToolbarButtonProps } from '../canvas/ToolbarButton';

export default class MockToolbarButtonProps {

  public static getToolbarButtons(count: number): IToolbarButtonProps[] {
    const toolbarButtonMocks: IToolbarButtonProps[] = [];
    const onClick: Sinon.SinonSpy = sinon.spy();
    const onKeyDown: Sinon.SinonSpy = sinon.spy();
    for (let ct: number = 0; ct < count; ct++) {
      const strCt: string = ct.toString();
      toolbarButtonMocks.push({
        onClick: onClick,
        disabled: false,
        isActive: true,
        key: strCt + '-key',
        tabIndex: 0,
        onKeyDown: onKeyDown,
        automationId: strCt + '-id',
        title: strCt + '-title'
      });
    }

    return toolbarButtonMocks;
  }
}
